*{
    scroll-behavior: smooth;
    font-family: "Lucida Console", "Courier New", monospace;
}
::-webkit-scrollbar {
    width: 10px;
}



@import url("https://fonts.googleapis.com/css?family=Raleway:900&display=swap");



body {
    margin: 0px;
}
:root {
    --clr-1 : #052b2f;
    --clr-2 : #073438;
    --clr-3 : #0e4b50;
    --clr-4 : #2d8f85;
    --clr-5 : #638c54;
}

#socialmedia::before, #socialmedia::after  {
    content: "";
    inset: 0;
    border-radius: inherit;
    background: linear-gradient(
        0deg,
        var( --clr-3),
        var( --clr-4),
        var( --clr-5)
    );
}

#container {
    position: absolute;
    margin: auto;
    width: 100vw;
    height: 80pt;
    top: 0;
    bottom: 0;

    filter: url(#threshold) blur(0.6px);
}

#text1,
#text2 {
    position: absolute;
    width: 100%;
    display: inline-block;

    font-family: "Raleway", sans-serif;
    font-size: 80pt;

    text-align: center;

    user-select: none;
}
  
/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}
  
/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 5px;
}
  
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}
#about::-webkit-scrollbar{
    display: none;
}
